import glamorous from 'glamorous';
import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import { hexToRGB } from 'utils/helpers';
import * as glamor from 'glamor';

const videoIconWhite = require('../assets/video_icon_white.svg');
const videoIconBlack = require('../assets/video_icon_black.svg');

const { mediaQueries, screenSizes, padding } = utils;
const fontSize = '19px';
const cardMargin = 8;
export const spanBottomMargin = 2;

const slideFade = glamor.keyframes({
  '100%': {
    transform: 'translateY(0)',
    opacity: 1,
  },
});

const opacityFade = glamor.keyframes({
  '0%': {
    transform: 'translateY(0)',
    opacity: 1,
  },
  '100%': {
    transform: 'translateY(0)',
    opacity: 0,
  },
});

export const cardWrapper = (animate = false, isUpdating = false, { ...background }, isDesktop = true, type, isFeatured = false, isThreeUp = false) => ({
  cursor: `pointer`,
  position: 'relative',
  overflow: 'hidden',
  fontSize: fontSize,
  lineHeight: null,
  display: 'block',
  transition: 'all .2s linear',
  letterSpacing: utils.letterSpacing,
  marginBottom: cardMargin,
  background: background.background,
  color: background.fontColor,
  backgroundSize: 'cover',
  backgroundRepeat: 'no-repeat',
  backgroundPosition: background.position,
  transform: animate ? 'translateY(1em)' : 'initial',
  opacity: animate ? 0 : 1,
  animation: animate ? isUpdating ? `${opacityFade} 0.5s ease-in-out forwards` : `${slideFade} 0.3s ease-in-out forwards` : 'none',
  [`${mediaQueries.phablet}`]: {
    marginBottom: cardMargin,
  },
  '& .card-cta span': {
    background: `url(${background.arrowColor})`,
  },
  '& .content-span:before': {
    content: `''`,
    opacity: 0,
    background: 'linear-gradient(to right, rgba(255, 255, 255, 0) 50%, rgba(255, 255, 255, 1) 50%)',
    backgroundSize: '200%',
    transition: '.4s ease-in-out',
    backgroundPosition: '0 0',
    position: 'absolute',
    left: 0,
    height: '100%',
    width: '100%',
    zIndex: -1,
    borderRadius: 4,
  },
  '&:hover': isDesktop && {
    '& .card-subhead-icon': {
      background: `url(${videoIconBlack})`,
      backgroundRepeat: 'no-repeat',
      backgroundSize: 'contain',
    },
    '& .cs-subhead': {
      opacity: 0,
    },
    '& .content-span': {
      color: colors.black,
    },
    '& .content-span:before': {
      opacity: 1,
      backgroundPosition: '-100% 0',
    },
    '& .card-content': {
      bottom: 50,
      opacity: !isFeatured && type === 'creativeSpotlightDevice' && 1,
    },
    '& .card-cta': {
      opacity: 1,
    },
    '& .card-image': {
      '&:after': {
        opacity: !isFeatured && type === 'successStory' || isThreeUp && type === 'successStory'  ? 0 : 1,
        transform: 'scale(1)',
      },
      '&:before': {
        opacity: 1,
        transform: 'scale(1)',
      },
    },
  },
});

export const cardContent = (isDesktop = true, isFeatured = false, type, isMobile = false) => ({
  position: 'absolute',
  bottom: isDesktop ? 8 : 32,
  transition: 'all .2s ease-out',
  zIndex: 1,
  opacity: !isFeatured && type === 'creativeSpotlightDevice' && isDesktop && 0,
  padding: !isMobile ? '0 44px' : '0 32px',
});

export const cardTitle = () => ({
  fontSize: 24,
  lineHeight: '32px',
  fontWeight: 'normal',
  fontFamily: 'AvenirNext-Medium',
  display: 'inline-block',
  padding: '8px 0 0',
  // Below are custom breakpoints for grid card titles to account for text overlapping logos at specific widths
  [`@media (max-width:899px) and (min-width:${screenSizes.phablet}px)`]: {
    fontSize: 20,
    lineHeight: '24px',
  },
  [`@media (max-width:1072px) and (min-width:${screenSizes.normal}px)`]: {
    fontSize: 18,
    lineHeight: '22px',
  },
  [`@media (max-width:1248px) and (min-width:1073px)`]: {
    fontSize: 20,
    lineHeight: '24px',
  },
});

export const cardSubheadIcon = () => ({
  display: 'inline-block',
  height: 12,
  width: 12,
  background: `url(${videoIconWhite})`,
  backgroundRepeat: 'no-repeat',
  backgroundSize: 'contain',
  position: 'relative',
  marginRight: 8,
  top: 2,
});

export const cardSubhead = () => ({
  padding: '1rem 0 0',
  fontSize: 12,
  fontWeight: 'normal',
  fontFamily: 'AvenirNext-DemiBold',
  letterSpacing: '0.2px',
  '& .content-span-wrapper': {
    paddingBottom: 2,
  },
});

export const creativeSpotlightSubhead = () => ({
  position: 'absolute',
  bottom: '10%',
  left: '50%',
  transform: 'translateX(-50%)translateY(-50%)',
  transition: 'all .3s ease-out',
});

export const cardCTA = (isDesktop = true) => ({
  padding: '1rem 0 0',
  margin: '0 5px',
  fontSize: 12,
  fontWeight: 'normal',
  fontFamily: 'AvenirNext-Bold',
  letterSpacing: '1.4px',
  textTransform: 'uppercase',
  opacity: isDesktop ? 0 : 1,
});

export const cardCtaIcon = () => ({
  display: 'inline-block',
  height: 12,
  width: 12,
  backgroundRepeat: 'no-repeat',
  backgroundSize: 'contain',
  position: 'relative',
  marginLeft: 16,
  top: 2,
});

export const mobileLogoImage = (type, isMobile) => ({
  display: 'none',
  width: 160,
});

export const cardImage = ({...image}, isFeatured, type) => ({
  backgroundImage: `url(${image.image})`,
  backgroundRepeat: 'no-repeat',
  backgroundPosition: image.position,
  backgroundSize: image.size,

  '&:after': image.logoImage && {
    content: !isFeatured && type === 'creativeSpotlightDevice' ? `` :  `''`,
    position: 'absolute',
    top: 0,
    height: '100%',
    width: '100%',
    backgroundImage: `url(${image.logoImage})`,
    backgroundRepeat: 'no-repeat',
    backgroundPosition: image.position,
    backgroundSize: image.size,
    transition: 'all .3s ease-out',
    backfaceVisibility: 'hidden',
  },
  '&:before': image.featuredImage && {
    content: `''`,
    backgroundImage: `url(${image.featuredImage})`,
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center center',
    backgroundSize: 'contain',
    position: 'absolute',
    height: '80%',
    width: '100%',
    top: 0,
    opacity: image.type !== 'creativeSpotlightDevice' ? 0 : 1,
    transform: image.type !== 'creativeSpotlightDevice' && 'scale(.9)',
    transition: 'all .3s ease-out',
    backfaceVisibility: 'hidden',
  },
});

export const cardCopy = (article: boolean = false) => ({
  color: colors.regularCharcoal,
  fontSize: 16,
  lineHeight: '24px',
  padding: '0.5rem 2rem 0',
  boxSizing: 'border-box',
  paddingBottom: article && 42,
  [`${mediaQueries.phablet}`]: {
    fontSize: !article ? 19 : 16,
    lineHeight: !article ? '28px' : '24px',
  },
});

export const superscript = () => ({
  transform: 'translateY(-20%)',
  display: 'inline-block',
  fontSize: '50%',
  verticalAlign: 'top',
});

export const contentContainer = (isLoaded = false) => ({
  display: 'block',
  opacity: isLoaded ? 1 : 0,
  transition: 'opacity .3s ease-out',
});

export const contentSpanWrapper = () => ({
  display: 'table',
  position: 'relative',
  marginBottom: spanBottomMargin,
  padding: '0 4px',
});



