import * as React from 'react';
import * as glamorous from 'glamorous';
import * as ReactDOM from 'react-dom';

import {spanBottomMargin} from '../../base/GridCard-base-styles';

const { Div, Span } = glamorous;

import { contentContainer, contentSpanWrapper} from '../../base/GridCard-base-styles';

export interface IContentContainerProps {
  children?: React.ReactNode;
  iterateContent?: Function;
  content: string;
  windowWidth?: number;
}

export interface IContentContainerState {
  prevHeight: number;
  content: string;
  prevContent: string;
  breakList: Array<number>;
  renderedContent: string;
  renderedContentIndex: number;
  isLoaded: boolean;
}

export class ContentContainer extends React.Component<IContentContainerProps, IContentContainerState> {

  state = {
    prevHeight: 0,
    prevContent: '',
    content: '',
    breakList: [],
    renderedContent: this.props.content ? this.props.content.split(' ')[0] : '',
    renderedContentIndex: 0,
    isLoaded: false,
  };

  el: any;

  init() {
    if (this.el) {
      this.setState({prevHeight: this.el.offsetHeight}, () => {
        this.iterateContent();
      });
    }
  }

  componentDidMount() {
    // Timout necessary for Safari to calculate page
    setTimeout(() => {
      this.init();
      this.setState({isLoaded: true});
    }, 200);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.windowWidth !== this.props.windowWidth) {
      this.setState((state) => {
        return {
          prevHeight: 0,
          prevContent: '',
          content: '',
          breakList: [],
          renderedContent: this.props.content ? this.props.content.split(' ')[0] : '',
          renderedContentIndex: 0,
        };
      }, () => {
        this.init();
      });
    }
  }

  componentDidUpdate (prevProps, prevState) {

    if (prevState.renderedContent !== this.state.renderedContent) {

      let breakList = [...this.state.breakList];

      if (this.state.prevHeight !== this.el.offsetHeight && this.el.offsetHeight > this.state.prevHeight + spanBottomMargin && this.state.prevHeight !== 0) {

        const array = prevState.renderedContent.split(' ');

        breakList = [...this.state.breakList, array.length - 1];

      }

      this.setState((state) => {
        return {
          prevHeight: this.el.offsetHeight,
          prevContent: prevState.renderedContent,
          breakList: breakList,
        };
      });

      this.iterateContent();

    }

  }

  iterateContent() {

    const stringLength = this.props.content.split(' ').length;

    if (this.state.renderedContentIndex < stringLength - 1 ) {
      this.setState((state) => {
        return {
          renderedContent: this.state.renderedContent + ' ' + this.props.content.split(' ')[this.state.renderedContentIndex + 1],
          renderedContentIndex: this.state.renderedContentIndex + 1,
        };
      });
    }
  }

  setHtml (content) {

    const spanElements = [];
    const contentArray = content.split(' ');

    this.state.breakList.forEach((postion, index) => {

      let span;

      if (index === 0) {
        span = contentArray.slice(0, postion + 1);
      } else {
        span = contentArray.slice(this.state.breakList[index - 1] + 1, postion + 1);
      }

      spanElements.push(span.join(' '));
    });

    if (this.state.breakList.length) {
      const remainderArray = contentArray.slice(this.state.breakList[this.state.breakList.length - 1] + 1, contentArray.length);

      spanElements.push(remainderArray.join(' '));
    }

    return (
      <Span innerRef={ (el) => this.el = el } css={contentContainer(this.state.isLoaded)}>
        {this.state.breakList.length ? (

          spanElements.map((spanContent, i) => {
            return (
              <Span key={i} className={'content-span-wrapper'} css={contentSpanWrapper()}>
                { i === 0 ? this.props.children : null }
                <Span className={'content-span'}>
                  {spanContent}
                </Span>
              </Span>
            );
          })
        ) : (
          <Span className={'content-span-wrapper'} css={contentSpanWrapper()}>
            {this.props.children}
            <Span className={'content-span'}>
              {content}
            </Span>
          </Span>
        )}
      </Span>
    );

  }

  render () {
    return (
      this.setHtml(this.state.renderedContent)
    );
  }

}



