import * as React from 'react';
import * as ReactSelect from 'react-select';
import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import glamorous from 'glamorous';
import * as glamor from 'glamor';

const scaleUp = glamor.keyframes({
  '100%': {
    maxHeight: '10.75em',
  },
});

export const paddingStyle = {
  transition: 'padding .2s linear',
  [utils.mediaQueries.phablet]: {
    padding: '0 40px 0 32px',
  },
  [utils.mediaQueries.phabletMax]: {
    padding: '0 24px',
  },
};

export const ReactSelectGlam = glamorous(ReactSelect as React.ComponentClass<any>)({
  boxShadow: '0 2px 4px rgba(0,0,0, .08)',
  borderRadius: '32px',
  '& .Select-control': {
    transition: 'padding .2s linear',
    borderRadius: '32px',
    borderWidth: 0,
  },
  '&.is-open > .Select-control': {
    borderRadius: '32px 32px 0 0',
  },
  '& .Select-menu-outer': {
    borderRadius: '0 0 32px 32px!important',
    borderWidth: 0,
    boxShadow: '0 2px 4px rgba(0,0,0, .08)',
  },
  '& .Select-menu': {
    maxHeight: 0,
    overflowY: 'auto',
    transition: 'all .2s ease .2s',
    borderRadius: '0 0 32px 32px',
    animation: `${scaleUp} .2s ease-in-out forwards`,
  },
  '& .Select-option': {
    transition: 'padding .2s linear',
    [utils.mediaQueries.normal]: {
      padding: '16px 32px',
    },
    [utils.mediaQueries.normalMax]: {
      padding: '16px 24px',
    },
  },
  '& .Select-arrow': {
    borderColor: `${colors.black} transparent transparent`,
  },
  '&.Select--single > .Select-control .Select-value, .Select-placeholder': {
    ...paddingStyle,
  },
  '& .Select-option.is-selected': {
    backgroundColor: 'rgba(0,0,0,.02)',
    color: colors.black,
  },
  '& .Select-option.is-focused': {
    backgroundColor: 'rgba(0,0,0,.04)',
    color: colors.black,
  },
  '& .Select-input > input': {
    padding: '12px 32px',
    [utils.mediaQueries.normalMax]: {
      padding: '12px 24px',
    },
  },
  '& .Select-arrow-zone': {
    paddingRight: '24px',
    [utils.mediaQueries.phabletMax]: {
      paddingRight: '24px',
    },
  },
});



